@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";











































.btb_layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%;
  width: 100%;

  .layout_head {
    flex-shrink: 0;
  }
  .layout_body {
    flex-grow: 1;
    display: flex;
    flex-direction: row;

    .body_container {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      overflow: auto;

      .container_content {
        flex-grow: 1;
      }
      .container_foot {
        flex-shrink: 0;
      }
    }
    .body_aside {
      width: 300px;
      flex-shrink: 0;
    }
  }
}
